/* COLORS */
$mocha-op: rgba(255, 231, 196, 0.7rem);
$dark-brown: #69440b;
$darkest-brown: #543d37;

$isabelline: #FBF2EE;
$champagne: #F7E6DE;
$champagne-darker: #F3DACE;
$champagne-darkest: #d1b5ac;
$desert-sand: #EDC4B3;
$tumbleweed: #DEAB90;
$antique-brass: #C38E70;
$cafe-au-lait: #B07D62;
$brown-sugar: #9d6b53;
$browner-sugar: #835945;
$coffee: #8A5A44;
$coffee-darker: #714533;
$coffee-darkest: #633C2C;

/* FONTS */
@import url("https://fonts.googleapis.com/css2?family=Calistoga&family=El+Messiri:wght@500&family=Pangolin&family=Special+Elite&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@100;900&display=swap");
// font-family: 'Calistoga', cursive;
// font-family: 'Pangolin', cursive;
// font-family: 'Special Elite', cursive;
// font-family: 'El Messiri', sans-serif;
// font-family: 'Londrina Solid', cursive;

$border: 0.15rem;

/* BASIC RULES */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    list-style: none;
}

html {
    height: 100%;
}

body {
    width: 100%;
    font-family: 'El Messiri', sans-serif;
    background-color: $champagne;
    color: $coffee;
    position: absolute;
    min-height: 100%;
    padding-bottom: 8rem;
}

a {
    text-decoration: none;
    color: $brown-sugar;
}

a:hover {
    color: $coffee-darker
}

ul {
    list-style: none;
}

button:focus {
    outline: none;
}


@media screen and (max-width: 768px) {
    body {
        padding-bottom: 7.5rem;
    }
}
