@import 'index';

.to-list {
    cursor: pointer;
    background-color: $champagne-darker;
    border: none;
    color: $brown-sugar;
    font-size: 1.2rem;
    margin: 0.7rem 2rem;
    padding: 0.3rem;
    transition: all .3s ease-in-out;
}

.to-list:hover {
    color: $coffee-darker;
    transform: scale(1.15);
}